// src/blocks/intro/Intro.jsx
import React, { useState } from "react";
import { useTheme } from "@mui/material/styles";
import { 
  Box, 
  Button, 
  Typography, 
  useMediaQuery,
  Skeleton
} from "@mui/material";
import { makeStyles } from "@mui/styles";

// Define Styles - keeping your original styling
const useStyles = makeStyles((theme) => ({
  container: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "column",
    backgroundColor: theme.palette.background.default,
  },
  heroImage: {
    maxWidth: "100%",
    height: "auto",
    maxHeight: "100vh",
  },
  heroTextImage: {
    height: "auto",
  },
  description: {
    color: theme.palette.text.primary,
    textAlign: "center",
    marginTop: 16,
  },
  button: {
    color: theme.palette.primary.main,
    borderColor: theme.palette.primary.main,
    marginTop: 24,
    padding: "8px 16px",
    fontFamily: '"Preahvihear", sans-serif',
    "&:hover": {
      backgroundColor: theme.palette.primary.light,
      color: theme.palette.background.default,
    },
  },
  imageSkeleton: {
    width: "100%",
    height: "60vh",
    borderRadius: 4,
    marginBottom: 10,
    backgroundColor: theme.palette.mode === "dark" ? 
      "rgba(255, 255, 255, 0.1)" : 
      "rgba(0, 0, 0, 0.05)",
  }
}));

export default function Intro() {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("md"));
  const classes = useStyles({ isMobile });
  const [imageLoaded, setImageLoaded] = useState(false);

  // Switch images based on theme mode - keeping your original code
  const heroImageSrc = theme.palette.mode === "dark" ? 
    "/assets/hero4xdark.png" : 
    "/assets/hero4x.png";
  const heroTextSrc = theme.palette.mode === "dark" ? 
    "/assets/herotextdark.png" : 
    "/assets/herotext1x.png";
  const width = isMobile ? '200vw' : '130vw';

  const handleImageLoad = () => {
    setImageLoaded(true);
  };

  return (
    <Box className={classes.container} id="intro">
      {/* Skeleton shown while image is loading */}
      {!imageLoaded && (
        <Skeleton
          variant="rectangular"
          className={classes.imageSkeleton}
          animation="wave"
          style={{ width: width, height: isMobile ? "40vh" : "60vh" }}
        />
      )}
      
      {/* Hero Image - with onLoad handler to manage loading state */}
      <Box
        component="img"
        src={heroImageSrc}
        alt="soumya in a hoodie"
        style={{
          width: width, 
          height: "inherit", 
          marginLeft: -44,
          display: imageLoaded ? 'block' : 'none' // Hide until loaded
        }}
        loading="lazy"
        onLoad={handleImageLoad}
      />
    </Box>
  );
}